<h1 id="android-activities" tabindex="-1"><a class="header-anchor" href="#android-activities" aria-hidden="true">#</a> Android Activities</h1>
<p>Activities are one of the four different fundamental application components all Android applications consists of. We use them to specify what the graphical user interface (GUI) should look like. Let's take a look at how they work!</p>
