<h2 id="activities" tabindex="-1"><a class="header-anchor" href="#activities" aria-hidden="true">#</a> Activities</h2>
<p>An <em>activity</em> is a fundamental application component you can use to show a graphical user interface to the user, and which the user can interact with to give input to your application. Your application usually consist of multiple activities, each with its own purpose, such as:</p>
<ul>
<li>One activity to display and handle interaction with a login form</li>
<li>One activity to display and handle interaction with a registration form</li>
<li>One activity to display and handle interaction with all accounts in the application</li>
<li>Etc.</li>
</ul>
<p>Only one activity at a time is shown on the user's screen.</p>
<h3 id="creating-a-new-activity" tabindex="-1"><a class="header-anchor" href="#creating-a-new-activity" aria-hidden="true">#</a> Creating a new activity</h3>
<p>A general activity is represented by the Android framework class <code>Activity</code>. When you want to create your own activity, you should create your own class that inherits from the <code>Activity</code> class, and in which you override some methods to give your own activity the specific behavior you want it to have.</p>
<div class="custom-container warning"><p class="custom-container-title">Follow conventions!</p>
<p>In Android projects, there's a convention to let all your own activity classes end with the name <code>Activity</code>, such as <code>LoginActivity</code>. Good programmers follow this convention, so their code is easier to understand for other programmers reading it.</p>
</div>
<p>Just creating a new class that inherits from the <code>Activity</code> class is not enough for the Android framework to recognize your class as an activity. You must also register this class as an activity in the <code>AndroidManifest.xml</code> file using the <code>&lt;activity&gt;</code> element in the <code>&lt;application&gt;</code> element (further down is an example of all this). When using the <code>&lt;activity&gt;</code> element, you must add the following attributes to it:</p>
<ul>
<li><code>android:name</code>, which should be:
<ul>
<li>The full identifier for the class (i.e package name followed by the class name, e.g. <code>se.ju.larpet.myapp.LoginActivity</code>), or:</li>
<li>An identifier for the class relative the your application's package name (e.g. if you have <code>&lt;manifest android:package=&quot;se.ju.larpet.myapp&quot;</code>), then you can just write <code>.LoginActivity</code></li>
</ul>
</li>
<li><code>android:exported</code>, which should be:
<ul>
<li><code>true</code>, if you want the OS/other applications to be able to start the activity (typically used for the activity that should be started when the user starts your application)</li>
<li><code>false</code>, if you only want your own code to be able to start the activity (typically the case for most of your activities)</li>
</ul>
</li>
</ul>
<div class="custom-container tip"><p class="custom-container-title">Creating a new activity in Android Studio</p>
<p>When you ask Android Studio to create a new activity for you, it will create a new Java/Kotlin file containing your own class inheriting from the <code>Activity</code> class AND register this class as an activity in <code>AndroidManifest.xml</code> for you, so you don't need to remember all this by heart.</p>
</div>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>Example showing a project with a single activity only your own app can start.</p>
<CodeGroup>
<CodeGroupItem title="AndroidManifest.xml">
<div class="language-xml ext-xml line-numbers-mode"><pre v-pre class="language-xml"><code><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>manifest</span>
	<span class="token attr-name"><span class="token namespace">xmlns:</span>android</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>http://schemas.android.com/apk/res/android<span class="token punctuation">"</span></span>
	<span class="token attr-name">package</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>se.ju.larpet.myapp<span class="token punctuation">"</span></span><span class="token punctuation">></span></span>
	
	<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>application</span> <span class="token attr-name">...</span><span class="token punctuation">></span></span>
		
		<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>activity</span>
			<span class="token attr-name"><span class="token namespace">android:</span>name</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>.MyActivity<span class="token punctuation">"</span></span>
			<span class="token attr-name"><span class="token namespace">android:</span>exported</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>false<span class="token punctuation">"</span></span> <span class="token punctuation">/></span></span>
		
	<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>application</span><span class="token punctuation">></span></span>
	
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>manifest</span><span class="token punctuation">></span></span>
</code></pre><div class="highlight-lines"><br><br><br><br><br><br><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div><br><br><br><br></div><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br></div></div></CodeGroupItem>
<CodeGroupItem title="MyActivity.kt">
<div class="language-kotlin ext-kt line-numbers-mode"><pre v-pre class="language-kotlin"><code><span class="token keyword">import</span> android<span class="token punctuation">.</span>app<span class="token punctuation">.</span>Activity

<span class="token keyword">class</span> MyActivity <span class="token operator">:</span> <span class="token function">Activity</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
	
	<span class="token comment">// Override methods here to give your own activity</span>
	<span class="token comment">// the specific behavior you want it to have.</span>
	
<span class="token punctuation">}</span>
</code></pre><div class="highlight-lines"><br><br><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div></div><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br></div></div></CodeGroupItem>
</CodeGroup>
</div>
<p>The <code>&lt;activity&gt;</code> element has more attributes you can use to add more information about the activity, such as:</p>
<ul>
<li><code>android:label</code>: A string with the name of the activity (used for example when the user is asked to select an activity to complete an action)</li>
<li><code>android:theme</code>: A theme to use in the activity (when you want another one than the one specified in <code>&lt;application android:theme=&quot;XXX&quot;&gt;</code>)</li>
<li>Etc.</li>
</ul>
<p>Most often you don't need to provide these extra attributes, but do be aware of the possibility.</p>
<h3 id="adding-an-activity-to-the-launcher" tabindex="-1"><a class="header-anchor" href="#adding-an-activity-to-the-launcher" aria-hidden="true">#</a> Adding an activity to the launcher</h3>
<p>The launcher app in Android is the &quot;desktop&quot; app that runs directly when the device starts, and that app typically shows all installed apps the user can start by clicking on them. It actually doesn't show apps that can be started, but activities in them that can be started, so one and the same app can actually display multiple activities in the launcher, and not just one, but just one is of course most common.</p>
<p>So, if you want the launcher to show one of your activities here, the activity must have <code>android:exported=&quot;true&quot;</code> (so the other apps are allowed to start the activity). Then you must also add an <code>&lt;intent-filter&gt;</code> to your activity to tell other apps information about the activity, such as:</p>
<ul>
<li>The <code>android:name</code> attribute in the <code>&lt;category&gt;</code> element to indicate which a category the activity belongs to (for launcher activities, use the value <code>android.intent.category.LAUNCHER</code>)</li>
<li>The <code>android:name</code> attribute in the <code>&lt;action&gt;</code> element to indicate which <em>actions</em> the activity can handle (the launcher app will start the activity with the action <code>android.intent.action.MAIN</code> (meaning no additional info is passed to the activity), so your activity must explicitly say it can handle that type of action)</li>
</ul>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>Example showing a project with one launcher activity.</p>
<CodeGroup>
<CodeGroupItem title="AndroidManifest.xml">
<div class="language-xml ext-xml line-numbers-mode"><pre v-pre class="language-xml"><code><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>manifest</span>
	<span class="token attr-name"><span class="token namespace">xmlns:</span>android</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>http://schemas.android.com/apk/res/android<span class="token punctuation">"</span></span>
	<span class="token attr-name">package</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>se.ju.larpet.myapp<span class="token punctuation">"</span></span><span class="token punctuation">></span></span>
	
	<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>application</span> <span class="token attr-name">...</span><span class="token punctuation">></span></span>
		
		<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>activity</span>
			<span class="token attr-name"><span class="token namespace">android:</span>name</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>.MyActivity<span class="token punctuation">"</span></span>
			<span class="token attr-name"><span class="token namespace">android:</span>exported</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>true<span class="token punctuation">"</span></span><span class="token punctuation">></span></span>
			
			<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>intent-filter</span><span class="token punctuation">></span></span>
				<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>category</span> <span class="token attr-name">name</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>android.intent.category.LAUNCHER<span class="token punctuation">"</span></span> <span class="token punctuation">/></span></span>
				<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>action</span> <span class="token attr-name">name</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>android.intent.action.MAIN<span class="token punctuation">"</span></span> <span class="token punctuation">/></span></span>
			<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>intent-filter</span><span class="token punctuation">></span></span>
			
		<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>activity</span><span class="token punctuation">></span></span>
		
	<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>application</span><span class="token punctuation">></span></span>
	
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>manifest</span><span class="token punctuation">></span></span>
</code></pre><div class="highlight-lines"><br><br><br><br><br><br><br><br><br><br><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div><br><br><br><br><br><br></div><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br></div></div></CodeGroupItem>
<CodeGroupItem title="MyActivity.kt">
<div class="language-kotlin ext-kt line-numbers-mode"><pre v-pre class="language-kotlin"><code><span class="token keyword">import</span> android<span class="token punctuation">.</span>app<span class="token punctuation">.</span>Activity

<span class="token keyword">class</span> MyActivity <span class="token operator">:</span> <span class="token function">Activity</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
	
	<span class="token comment">// Override methods here to give your own activity</span>
	<span class="token comment">// the specific behavior you want it to have.</span>
	
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br></div></div></CodeGroupItem>
</CodeGroup>
<p>The <code>MyActivity</code> will now be shown in the user's launcher, and when the user clicks on it, the <code>MyActivity</code> will be started.</p>
</div>
<h3 id="specifying-the-gui" tabindex="-1"><a class="header-anchor" href="#specifying-the-gui" aria-hidden="true">#</a> Specifying the GUI</h3>
<p>By default, the activity has no GUI, and a blank screen is shown when the activity is started. If you want to add a GUI to your activity, override the method <code>onCreate()</code>, and specify what the GUI should be by calling the method <code>setContentView()</code>. Most often the GUI is specified in XML files you put in the <code>res/layout/</code> folder, and to <code>setContentView()</code> you pass an identifier to the XML file to use for the GUI.</p>
<div class="custom-container warning"><p class="custom-container-title">Follow conventions!</p>
<p>In Android projects, there's a convention to name layouts that are used by activities as <code>activity_XXX.xml</code>, such as <code>activity_login.xml</code>. Good programmers follow this convention, so their code is easier to understand for other programmers reading it.</p>
</div>
<p><code>onCreate()</code> will be called with a <code>Bundle</code> as argument called <code>savedInstanceState</code>. That argument is very important to handle <em>runtime configuration changes</em> properly, which we will cover in another lecture. For now, it's enough for you to know that you must pass this <code>Bundle</code> to the <code>onCreate()</code> method you override.</p>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>Example showing how to create an activity with a GUI.</p>
<CodeGroup>
<CodeGroupItem title="AndroidManifest.xml">
<div class="language-xml ext-xml line-numbers-mode"><pre v-pre class="language-xml"><code><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>manifest</span>
	<span class="token attr-name"><span class="token namespace">xmlns:</span>android</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>http://schemas.android.com/apk/res/android<span class="token punctuation">"</span></span>
	<span class="token attr-name">package</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>se.ju.larpet.myapp<span class="token punctuation">"</span></span><span class="token punctuation">></span></span>
	
	<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>application</span> <span class="token attr-name">...</span><span class="token punctuation">></span></span>
		
		<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>activity</span>
			<span class="token attr-name"><span class="token namespace">android:</span>name</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>.MyActivity<span class="token punctuation">"</span></span>
			<span class="token attr-name"><span class="token namespace">android:</span>exported</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>true<span class="token punctuation">"</span></span><span class="token punctuation">></span></span>
			
			<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>intent-filter</span><span class="token punctuation">></span></span>
				<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>category</span> <span class="token attr-name">name</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>android.intent.category.LAUNCHER<span class="token punctuation">"</span></span> <span class="token punctuation">/></span></span>
				<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>action</span> <span class="token attr-name">name</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>android.intent.action.MAIN<span class="token punctuation">"</span></span> <span class="token punctuation">/></span></span>
			<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>intent-filter</span><span class="token punctuation">></span></span>
			
		<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>activity</span><span class="token punctuation">></span></span>
		
	<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>application</span><span class="token punctuation">></span></span>
	
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>manifest</span><span class="token punctuation">></span></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br></div></div></CodeGroupItem>
<CodeGroupItem title="res/layout/activity_my.xml">
<div class="language-xml ext-xml line-numbers-mode"><pre v-pre class="language-xml"><code><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>LinearLayout</span>
	<span class="token attr-name"><span class="token namespace">xmlns:</span>android</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>http://schemas.android.com/apk/res/android<span class="token punctuation">"</span></span>
	<span class="token attr-name"><span class="token namespace">android:</span>layout_width</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>match_parent<span class="token punctuation">"</span></span>
	<span class="token attr-name"><span class="token namespace">android:</span>layout_height</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>match_parent<span class="token punctuation">"</span></span>
	<span class="token attr-name"><span class="token namespace">android:</span>orientation</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>vertical<span class="token punctuation">"</span></span><span class="token punctuation">></span></span>
	
	<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>TextView</span>
		<span class="token attr-name"><span class="token namespace">android:</span>layout_width</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>wrap_content<span class="token punctuation">"</span></span>
		<span class="token attr-name"><span class="token namespace">android:</span>layout_height</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>wrap_content<span class="token punctuation">"</span></span>
		<span class="token attr-name"><span class="token namespace">android:</span>text</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>Hello, world!<span class="token punctuation">"</span></span> <span class="token punctuation">/></span></span>
	
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>LinearLayout</span><span class="token punctuation">></span></span>
</code></pre><div class="highlight-lines"><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div></div><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br></div></div></CodeGroupItem>
<CodeGroupItem title="MyActivity.kt">
<div class="language-kotlin ext-kt line-numbers-mode"><pre v-pre class="language-kotlin"><code><span class="token keyword">import</span> android<span class="token punctuation">.</span>app<span class="token punctuation">.</span>Activity
<span class="token keyword">import</span> android<span class="token punctuation">.</span>os<span class="token punctuation">.</span>Bundle

<span class="token keyword">class</span> MyActivity <span class="token operator">:</span> <span class="token function">Activity</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
	
	<span class="token keyword">override</span> <span class="token keyword">fun</span> <span class="token function">onCreate</span><span class="token punctuation">(</span>savedInstanceState<span class="token operator">:</span> Bundle<span class="token operator">?</span><span class="token punctuation">)</span> <span class="token punctuation">{</span>
		<span class="token keyword">super</span><span class="token punctuation">.</span><span class="token function">onCreate</span><span class="token punctuation">(</span>savedInstanceState<span class="token punctuation">)</span>
		
		<span class="token function">setContentView</span><span class="token punctuation">(</span>R<span class="token punctuation">.</span>layout<span class="token punctuation">.</span>activity_main<span class="token punctuation">)</span>
		
	<span class="token punctuation">}</span>
	
<span class="token punctuation">}</span>
</code></pre><div class="highlight-lines"><br><br><br><br><br><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div><br><br></div><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br></div></div></CodeGroupItem>
