<p>To learn more about how to create graphical user interfaces in Android, checkout the lecture <RouterLink to="/lectures/android-views-and-layouts/">Android Views and Layouts</RouterLink>.</p>
<h3 id="starting-another-activity" tabindex="-1"><a class="header-anchor" href="#starting-another-activity" aria-hidden="true">#</a> Starting another activity</h3>
<p>To navigate the user to another activity, call the method <code>startActivity()</code> on a <code>Context</code>. <code>Activity</code> inherits from <code>Context</code>, so you can call <code>startActivity()</code> on the <code>Activity</code> itself. When you call this method, you pass it an <code>Intent</code> indicating which other activity in your app to start. To the <code>Intent</code> constructor you pass a <code>Context</code> (e.g. the <code>Activity</code> itself) and the runtime class of the <code>Activity</code> that should be started, like <code>LoginActivity::class.java</code> to start the <code>LoginActivity</code>.</p>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>Example showing how to create an activity with a GUI.</p>
<CodeGroup>
