<h3 id="passing-information-to-another-activity" tabindex="-1"><a class="header-anchor" href="#passing-information-to-another-activity" aria-hidden="true">#</a> Passing information to another activity</h3>
<p>Often when you start another activity you need to pass additional information to the activity you start. For example, maybe you have:</p>
<ul>
<li><code>ListNewsArticlesActivity</code>, which list news articles, and when you click on one of them the user should come to:</li>
<li><code>ViewNewsArticleActivity</code>, which shows all information about a specific news article</li>
</ul>
<p><code>ViewNewsArticleActivity</code> needs to know which article to show information about, so when you start this activity, you need to pass along an identifier to the article to show. That type of information can be passed in a <code>Bundle</code> in the <code>Intent</code> you use to start the activity. <code>Intent</code> has a method called <code>putExtra()</code>, that you can call to add such information to the <code>Bundle</code> in the <code>Intent</code>. When you call <code>putExtra()</code> you pass it two arguments:</p>
<ul>
<li>The first argument is a string used as a key for the value you want to pass along</li>
<li>The second argument is the actual value you want to pass along</li>
</ul>
<p>The receiving activity can then through the <code>Intent</code> starting it (stored in the class' <code>intent</code> property) obtain the value passed along using the same key by calling a <code>get*Extra()</code> method (like <code>getIntExtra()</code>, or <code>getStringExtra()</code>, etc., depending on the datatype of the value). When calling the <code>get*Extra()</code> you also need to pass a value that should be returned if the one starting the activity forgot to pass along the extra information.</p>
<div class="custom-container warning"><p class="custom-container-title">Follow conventions!</p>
<p>In Android projects, the convention is to put the keys in constants named <code>EXTRA_XXX</code> in the receiving activity file, and always refer to these constants whenever you call <code>putExtra()</code> or <code>get*Extra()</code>. This way, you don't risking misspelling the name of the key, so you don't risk compiling an app that will crash when it runs. If you now misspell the constant instead, you will get a compiler error you are immediately aware of.</p>
<p>The constants also serve as documentation; by looking at the <code>EXTRA_XXX</code> constants you can easily see what extra information you need to pass along to the activity when you start it.</p>
</div>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>Example showing how to create an activity with a GUI.</p>
<CodeGroup>
<CodeGroupItem title="AndroidManifest.xml">
<div class="language-xml ext-xml line-numbers-mode"><pre v-pre class="language-xml"><code><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>manifest</span>
