<h3 id="passing-information-back" tabindex="-1"><a class="header-anchor" href="#passing-information-back" aria-hidden="true">#</a> Passing information back</h3>
<p>When an activity starts another activity, that other activity can pass information back to the first activity. This is useful when you need to re-use the same functionality at multiple places. For example, maybe the user should be able to select a color for multiple different parts of your application (one background color, one text color, one favorite color, etc.). Then you can put the &quot;Select color&quot; functionality in its own activity, and then other activities can start this activity whenever they need the user to select a color. When the user has selected her color, the activity sends back that color to the first activity.</p>
<p>To start an activity that should send back a value, call <code>startActivityForResult()</code> instead of <code>startActivity()</code>. In addition to the <code>Intent</code> identifying the activity to start, you should also pass along a <em>request code</em> (an integer) that you later will get back when the user has made her choice in the other activity. This is useful if the first activity makes multiple different calls to <code>startActivityForResult()</code> (the request code can be used to distinguish the calls).</p>
<div class="custom-container warning"><p class="custom-container-title">Follow conventions!</p>
<p>In Android projects, the convention is to put request codes in constants named <code>REQUEST_CODE_XXX</code> in the activity file calling <code>startActivityForResult()</code>. By using constants, you'll get a compiler error if you make a typing mistake, and it's easy to see which request code numbers the activity is using, so you don't risk re-using one and the same by mistake.</p>
</div>
<p>In the activity where the user should make her decision, call the method <code>setResult()</code> to indicate that the user has made her decision. Here you need to pass two values:</p>
<ul>
<li>A <em>result code</em> (integer) indicating if the selection succeeded or not (one typically use the constants <code>Activity.RESULT_OK</code> if the user made the choice, and <code>Activity.RESULT_CANCELED</code> if the user didn't/couldn't make a choice)</li>
<li>An <code>Intent</code> with more information about the choice the user made</li>
</ul>
<p>After the user has made her choice, you typically call <code>finish()</code> on the activity to close it, at which point the user will get back to the previous activity.</p>
<p>In the activity that called <code>startActivityForResult()</code>, override the method <code>onActivityResult()</code>, which will be called when the user gets back to the activity. Here you receive the following as arguments:</p>
<ul>
<li>The request code you used to start the activity</li>
<li>The result code</li>
<li>The <code>Intent</code> with more information about the user's choice</li>
</ul>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>Example showing how to create an activity with a GUI.</p>
